.actions{
    align-items: center;
    height: 38px;
    margin: 10px 20px 10px 20px;
    display: flex;
    align-content: center;
}
button, input{
    height: 100%;
}
input{
    width: 100%
}
.publish{
    height: 100%;
    margin-left: auto;
}
.add-input{
    width: 100%;
}
.mod-selector{
    display: flex;
    margin-left: 20px;
}
.mod-selector-button{
    border: 1px black solid;
    padding: 10px;
    cursor: pointer;
}
.selected-mod{
    background: darkgrey;
}
